<template>
	<div id="mathStudy">
		<a-input class="inputSection" style="width: 120px; text-align: center"  addon-before="题目数量" v-model="questionQuant"></a-input>

		<a-radio-group class="inputSection" :options="elementLengthGroup" v-model="elementLength"></a-radio-group>

		<a-radio-group class="inputSection" default-value="plusOnly" @change="modeChange">
			<a-radio-button value="plusOnly">只有加法</a-radio-button>
			<a-radio-button value="minusOnly">只有减法</a-radio-button>
			<a-radio-button value="combined">加减混合</a-radio-button>
		</a-radio-group>

		<a-button @click="freshQuests">出题</a-button>

		<div style="margin: 20px">
			<span v-for="item in questions" :key="item">
				<a-input class="equation" :addon-before="item"></a-input>
			</span>
		</div>

		<div style="margin: 20px">
			答案：{{ answers }}
		</div>

	</div>
</template>

<script>
function generateSymbols(_mode, _quant){
    let _output = [];
    switch (_mode){
	    case "random":
	        for (let i=0; i<_quant; i++ ){
	            let rand = Math.random();
                _output.push(rand>=0.5?"+":"-")
	        }
	        break;
	    case "+":
	        for (let i=0; i<_quant; i++ ){
                _output.push("+")
	        }
	        break;
	    case "-":
	        for (let i=0; i<_quant; i++ ){
                _output.push("-")
	        }
	        break;
    }
    return _output
}
function generateElements(_length, _quant){
    let _output = [];
    switch (_length){
	    case 1:
	        for (let i=0; i<_quant; i++ ){
	            let rand = Math.random() * 10;
                _output.push(String(rand)[0])
	        }
	        break;
	    case 2:
	        for (let i=0; i<_quant; i++ ){
                let rand = Math.random() * 100;
                _output.push(String(rand).slice(0, 2).replace(".", ""))
	        }
	        break;
	    case 3:
	        for (let i=0; i<_quant; i++ ){
                let rand = Math.random() * 1000;
                _output.push(String(rand).slice(0, 3).replace(".", ""))
	        }
	        break;
	    case 4:
	        for (let i=0; i<_quant; i++ ){
                let rand = Math.random() * 10000;
                _output.push(String(rand).slice(0, 4).replace(".", ""))
	        }
	        break;
    }
    return _output
}

/* eslint no-mixed-spaces-and-tabs: ["error", "smart-tabs"] */
/* eslint no-unused-vars: [0] */
import ARadioButton from "ant-design-vue/es/radio/RadioButton";
export default {
    name: 'mathStudy',
    components: {
        ARadioButton
    },
    data() {
        return {
            questionQuant: 30,
	        option: "plusOnly",
            elementLengthGroup: [{label: "≤10", value: 1}, {label: "≤100", value: 2}, {label: "≤1000", value: 3}, {label: "≤10000", value: 4}],
            elementLength: 1,
	        questions: null,
            answers: null,
        }
    },
    mounted() {
    },
    watch: {
    },
    methods: {
        modeChange: function (e){
            this.option = e.target.value;
        },
        freshQuests: function (){
            let that = this;
	        let symbols_1 = [];
	        let symbols_2 = [];
	        switch (this.option){
		        case "plusOnly":
                    symbols_1 = generateSymbols("+", parseInt(that.questionQuant));
                    symbols_2 = symbols_1;
                    break;
		        case "minusOnly":
                    symbols_1 = generateSymbols("-", parseInt(that.questionQuant));
                    symbols_2 = symbols_1;
                    break;
		        case "combined":
                    symbols_1 = generateSymbols("random", parseInt(that.questionQuant));
                    symbols_2 = generateSymbols("random", parseInt(that.questionQuant));
                    break;
	        }
            let elements_1 = generateElements(this.elementLength, parseInt(that.questionQuant));
            let elements_2 = generateElements(this.elementLength, parseInt(that.questionQuant));
            let elements_3 = generateElements(this.elementLength, parseInt(that.questionQuant));
	        let equations_toCal = [];
	        for (let i=0;i<that.questionQuant;i++){
	            let cache_toCal = [elements_1[i], symbols_1[i], elements_2[i], symbols_2[i], elements_3[i]];

	            if (eval(cache_toCal.join(" ")) < 0){
	                console.log()
	                cache_toCal[0] = String(Math.ceil(parseInt(cache_toCal[0]) - eval(cache_toCal.join(" ")) + Math.random()*10));
	            }
		        equations_toCal.push(cache_toCal);
	        }
	        let answers = [];
	        for (let i in equations_toCal){
                answers.push(eval(equations_toCal[i].join("")))
	        }
            console.log(answers)
            equations_toCal.forEach((ele, index)=>{
                equations_toCal[index] = ele.join(" ") + " = "
            });
            that.questions = equations_toCal;
            that.answers = answers;
        }
    },
    computed: {

    },
    filters: {

    }

}
</script>

<style scoped>
.inputSection {
	margin: 20px
}
.equation {
	width: 180px;
	margin: 10px;
	font-size: 26px;
}
</style>

