<template>
	<div id="homeworkContent">
		<!--配置项-->
		<a-row style="margin-top: 20px; margin-bottom: 20px;">
			<a-space direction="vertical">


				<a-input style="width: 1000px; text-align: left" addon-before="Spells" v-model="spellsCache"></a-input>
				<a-input style="width: 1000px; text-align: left" addon-before="Words" v-model="wordsCache"></a-input>
			</a-space>
		</a-row>
		<!--看字写拼音-->
		<div :style="cardBackDiv">
			<a-card title="看字写拼音">
				<span v-for="(item, i) in maximumElementQuant" v-bind:key="item">
					<span style="float:left" :style="blackboard">
						<a-row :style="spellClass">{{ spellsCache[i] }}</a-row>
						<a-row :style="wordClass">
							<span>{{ wordsCache[i] }}</span>
						</a-row>
					</span>
				</span>
			</a-card>
		</div>
		<!--组词-->
		<div :style="cardBackDiv">
			<a-card title="组词">
				<a-button @click="switchSelectAllWordsStatus">{{ $store.getters.getSelectAllWordsSwitchStatus('record_' + tabIndex) }}</a-button><!--是否全选按钮-->
				<a-row style="margin-top: 20px; margin-bottom: 20px;">
					<span v-for="(item, i) in $store.getters.getSelectedWords('record_'+tabIndex)" :key="'key_' + i"><!--已选关键字-->
						<a-button @click="DeleteSelectedWordByTagContent(item)">{{item}}</a-button><!--已选关键字取消按钮-->
					</span>
				</a-row>
				<a-row style="margin-top: 20px; margin-bottom: 20px;"><!--组词答题处-->
					<div v-for="i in $store.getters.getSelectedWords('record_'+tabIndex)" v-bind:key="i"
					     style="float: left; margin-right: 50px; margin-top: 20px">
						<span :style="phrasingSection">
							{{ i }}：&#09;<span><span v-for="j in $store.getters.getDuplicate" v-bind:key="j">_____{{ i }}_____&#09;；</span></span>
						</span>
					</div>
				</a-row>
			</a-card>
		</div>

	</div>
</template>

<script>
/* eslint no-mixed-spaces-and-tabs: ["error", "smart-tabs"] */
/* eslint no-unused-vars: [0] */
export default {
    name: 'homeworkContent',
    props: {
        tabIndex:{
            default: null
        },
        spells:{
            default: null
        },
    },
    data() {
        return {
            SelectAllWordsCache: null,
            phraseQuant: null,
	        cache: null
        }
    },
    mounted() {
        let that = this;
        this.spellsCache = this.spells;
        this.wordsCache = this.$store.getters.getWords("record_" + that.tabIndex);
        this.SelectAllWordsCache = this.$store.getters.getSelectAllWords["record_" + String(that.tabIndex)];
        this.phraseQuant = this.$store.getters.getPhraseQuant;
    },
    watch: {

    },
    methods: {
        switchSelectAllWordsStatus: function () {
            let that = this;
            let switchStatus = that.$store.getters.getSelectAllWordsSwitchStatus('record_' + that.tabIndex);
            if (switchStatus === "全选") {
                let cache = JSON.parse(JSON.stringify(that.wordsCache));
                this.$store.commit("updateSelectedWords", {key: "record_"+that.tabIndex, value: cache});
                this.$store.commit("updateSelectAllWordsSwitchStatus", {key: "record_"+that.tabIndex, value: "全不选"});
            } else {
                this.$store.commit("updateSelectedWords", {key: "record_"+that.tabIndex, value: []});
                this.$store.commit("updateSelectAllWordsSwitchStatus", {key: "record_"+that.tabIndex, value: "全选"});
            }
        },
        DeleteSelectedWordByTagContent: function (_target) {
            let that = this;
            this.$store.commit("deleteSelectedWordByContent", {recordName: "record_" + that.tabIndex, content: _target})
        },
    },
    computed: {
        spellsCache: function (){
            let that = this;
            return that.$store.getters.getSpells('record_' + that.tabIndex)
        },
        wordsCache: function (){
            let that = this;
            return that.$store.getters.getWords('record_' + that.tabIndex)
        },
        maximumElementQuant: function (){
            let that = this;
            return that.spellsCache.length >= that.wordsCache.length?that.spellsCache.length: that.wordsCache.length
        },
        // selectAllWordsSwitchStatus: function (){
        //     let that = this;
        //     return that.$store.getters.getSelectAllWordsSwitchStatus('record_' + that.tabIndex)
        // },
        spellClass: function () {
            return {
                "height": 33 * this.$store.getters.getScaleCoef + "px",
                "lineHeight": 20 * this.$store.getters.getScaleCoef + "px",
                "textAlign": "center",
                "verticalAlign": "middle",
                "fontSize": 26 * this.$store.getters.getScaleCoef + "px"
            }
        },
        wordClass: function () {
            return {
                "height": 95 * this.$store.getters.getScaleCoef + "px",
                "lineHeight": 95 * this.$store.getters.getScaleCoef + "px",
                "textAlign": "center",
                "verticalAlign": "middle",
                "fontSize": 40 * this.$store.getters.getScaleCoef + "px",
                // "fontFamily": "url(" + require('Comic Sans MS') + ")",
            }
        },
        blackboard: function () {
            return {
                backgroundImage: "url(" + require('../assets/metaTable.png') + ")",
                width: 100 * this.$store.getters.getScaleCoef + "px",
                height: 141 * this.$store.getters.getScaleCoef + "px",
                backgroundSize: "100%",
                backgroundRepeat: "no-repeat"
            }
        },
        cardBackDiv: function () {
            return {
                background: "gainsboro",
                marginTop: 24 * this.$store.getters.getScaleCoef + "px",
                marginBottom: 24 * this.$store.getters.getScaleCoef + "px",
                padding: 24 * this.$store.getters.getScaleCoef + "px",
            }
        },
        phrasingSection: function () {
            return {
                marginTop: 24 * this.$store.getters.getScaleCoef + "px",
                fontSize: 24 * this.$store.getters.getScaleCoef + "px",
            }
        }
    },
    filters: {

    }
}
</script>

<style scoped>

</style>
