<template>
	<div id="app">
		<a-layout :style="windowLayoutClass">
			<!--侧边栏-->
			<a-layout-sider :style="windowLayoutSiderClass">
				<a-menu :default-selected-keys="['Chinese']">
					<a-menu-item key="Chinese" @click="changeSelectedMenuItem($event)">识字</a-menu-item>
					<a-menu-item key="Math" @click="changeSelectedMenuItem($event)">算数</a-menu-item>
				</a-menu>
			</a-layout-sider>
			<!--语文-->
			<a-layout-content v-if="selectedMenuItemName === 'Chinese'">
				<chineseStudy></chineseStudy>
			</a-layout-content>
			<!--数学-->
			<a-layout-content v-else-if="selectedMenuItemName === 'Math'">
				<mathStudy></mathStudy>
			</a-layout-content>
		</a-layout>
	</div>
</template>
<script>
import chineseStudy from './components/chineseStudy.vue'
import mathStudy from './components/mathStudy.vue'

/* eslint no-mixed-spaces-and-tabs: ["error", "smart-tabs"] */
/* eslint no-unused-vars: [0] */
export default {
    name: 'App',
    components: {
        chineseStudy, mathStudy
    },
	data(){
        return {
            selectedMenuItemName: "Chinese"

        }
	},
	methods:{
        changeSelectedMenuItem: function(e){
            console.log(e.key)
            let that = this;
            that.selectedMenuItemName = e.key;
        }
	},
	computed: {
        windowLayoutClass:function (){
            return {
                "height": window.innerHeight + "px"
            }
        },
        windowLayoutSiderClass:function (){
            return {
                "height": window.innerHeight + "px",
                "text-align": "center",
                "width": "40px",
            }
        },
	}

}
</script>

<style scoped>

</style>

