import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
Vue.config.devtools = true;

export default new Vuex.Store({
    state:{
        scaleCoef: 0.6,
        duplicate: 3,
        spells: {"record_1": ["a", "b", "c"], "record_2": ["d", "e", "f"], },
        words: {"record_1": ["啊", "哦", "额", ], "record_2": ["以", "无", "于", ]},
        phraseQuant: 3,
        selectAllWords: {"record_1": "全选", "record_2": "全选"},
        selectedWords: {"record_1": [], "record_2": []}
    },
    mutations:{
        refreshStateBySelectedFile(state, _newDict){
            let that = this;
            that.state.spells = _newDict.spells;
            that.state.words = _newDict.words;
            that.state.selectAllWords = _newDict.selectAllSwitchStatus;
            that.state.selectedWords = _newDict.selectedWords;
        },
        updateScaleCoef(state, _newValue){
            let that = this;
            that.state.scaleCoef = _newValue.value;
        },
        updateDuplicate(state, _newValue){
            let that = this;
            that.state.duplicate = _newValue.value;
        },
        updateSelectedWords(state, _newValue){
            let that = this;
            that.state.selectedWords[_newValue.key] = _newValue.value;
        },
        updateSelectAllWordsSwitchStatus(state, _newValue){
            let that = this;
            that.state.selectAllWords[_newValue.key] = _newValue.value;
        },
        deleteSelectedWordByContent(state, _input){
            let that = this;
            let index = that.state.selectedWords[_input.recordName].indexOf(_input.content);
            that.state.selectedWords[_input.recordName].splice(index, 1);
        }
    },
    getters: {
        getRecordsQuant(state){
            let spellsRecordQuant = Object.keys(state.spells).length;
            let wordsRecordQuant = Object.keys(state.words).length;
            return spellsRecordQuant >= wordsRecordQuant?spellsRecordQuant: wordsRecordQuant;
        },
        getSpells(state){
            return function(_recordIndex){
                return state.spells[_recordIndex]
            }
        },
        getWords(state){
            return function(_recordIndex){
                return state.words[_recordIndex]
            }
        },
        getSelectAllWordsSwitchStatus(state){
            return function(_recordIndex){
                return state.selectAllWords[_recordIndex]
            }
        },
        getSelectedWords(state){
            return function (_recordIndex){
                return state.selectedWords[_recordIndex]
            };
        },
        getDuplicate(state){
            return state.duplicate;
        },
        getScaleCoef(state){
            return state.scaleCoef;
        },
    }
});