                that.$store.commit("refreshStateBySelectedFile", {
                    "spells": spells_allRecords,
                    "words": words_allRecords,
                    "selectAllSwitchStatus": selectAllWordsSwitchStatus_allRecords,
                    "selectedWords": selectedWords_allRecords
                })
            }
